import React from "react";
import {
  makeStyles,
  Box,
  Typography,
  Hidden,
  IconButton,
} from "@material-ui/core";
import TextyAnim from "rc-texty";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";

import StaticDrawer from "./drawer";

const Projects = () => {
  const useStyles = makeStyles((theme) => ({
    root: {
      display: "flex",
    },
    toolbar: theme.mixins.toolbar,
    content: {
      flexGrow: 1,
    },
    button: { width: 0, color: "inherit" },
  }));

  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const accionAbrir = () => {
    setOpen(!open);
  };
  const getEnter = (e) => {
    switch (e.index) {
      default:
        return {
          opacity: 0,
        };
    }
  };
  return (
    <div className={classes.root}>
      <IconButton className={classes.button} onClick={() => accionAbrir()}>
        <ChevronRightIcon />
      </IconButton>

      <Hidden xsDown>
        <StaticDrawer variant="permanent" open={true} />
      </Hidden>
      <Hidden smUp>
        <StaticDrawer variant="temporary" open={open} onClose={accionAbrir} />
      </Hidden>
      <div className={classes.content}>
        <Box my={10}>
          <Typography variant="h3" align="center">
            <Box my={5}>
              <TextyAnim enter={getEnter} leave={getEnter}>
                Proyectos
              </TextyAnim>
            </Box>
          </Typography>
        </Box>
        <Box my={10}>
          <Typography variant="body1" align="center">
            <Box my={5}>
              <Typography variant="h4" align="center">
                GameReviews
              </Typography>
              <br />{" "}
              <a href="https://dinamoweb.000webhostapp.com">
                https://dinamoweb.000webhostapp.com
              </a>{" "}
              <br />
              Implemente lo aprendido en desarrollo web para crear una página
              donde podés subir las reseñas de un videojuegos que elijas y poder
              ver las reseñas que las demás personas suban. Tecnologías
              implementadas: Bootstrap, CSS, HTML, NodeJS, MongoDB, Mongoose,
              ExpressJS, Heroku, API restful.
            </Box>
            <Box my={5}>
              <Typography variant="h4" align="center">
                Listado de tareas
              </Typography>
              <br />
              <a href="https://github.com/Nahue256/ListadoTareas">
                https://github.com/Nahue256/ListadoTareas
              </a>
              <br />
              Desarrolle una aplicación web donde podes anotar tus tareas,
              acumulándolas y podes marcar estas tareas para eliminarlas una vez
              completadas. <br />
              Tecnologías implementadas: Bootstrap, CSS, HTML, Javascript.
            </Box>
            <Box my={5}>
              <Typography variant="h4" align="center">
                Pack de juegos
              </Typography>
              <br />
              <a href="https://github.com/Nahue256/Ej1">Ejercicio 1</a>-
              <a href="https://github.com/Nahue256/Ej2">Ejercicio 2</a>-
              <a href="https://github.com/Nahue256/Ej3">Ejercicio 3</a>
              <br />
              Desarrolle un pack de juegos basado en un video hecho por el
              youtuber holamundo, en donde propone realizar 5 ejercicios para el
              portfolio <br />
              Tecnologías implementadas: React, Bootstrap, CSS, HTML,
              Javascript.
            </Box>

            <Box my={5}>
              <Typography variant="h4" align="center">
                Portfolio
              </Typography>
              <br />
              <a href="https://nahue256.github.io/PortfolioPages/">
                https://nahue256.github.io/PortfolioPages/
              </a>
              <br />
              Desarrolle un portfolio de 0, donde las personas interesadas
              pueden entrar y ver informacion acerca de mi, y a que me dedico
              <br />
              Tecnologías implementadas: React, Material-UI, CSS, HTML,
              Javascript.
            </Box>
          </Typography>
        </Box>
      </div>
    </div>
  );
};

export default Projects;
