import React from "react";
import {
  Box,
  Typography,
  makeStyles,
  Hidden,
  IconButton,
} from "@material-ui/core";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";

import Title from "./title";
import Links from "./links";
import StaticDrawer from "./drawer";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  toolbar: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
  },
  button: { width: 0, color: "inherit" },
}));

const Main = () => {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const accionAbrir = () => {
    setOpen(!open);
  };
  return (
    <div className={classes.root}>
      <IconButton className={classes.button} onClick={() => accionAbrir()}>
        <ChevronRightIcon />
      </IconButton>

      <Hidden xsDown>
        <StaticDrawer variant="permanent" open={true} />
      </Hidden>
      <Hidden smUp>
        <StaticDrawer variant="temporary" open={open} onClose={accionAbrir} />
      </Hidden>

      <div>
        <Box my={15}>
          <Title />
          <Typography variant="h5" align="center">
            <Box my={5}>
              <Links />
            </Box>
          </Typography>
        </Box>
        <Box my={20}>
          <Typography variant="h4" align="center">
            Acerca de mi
          </Typography>
          <Typography variant="body1" align="center">
            Soy un estudiante y desarrollador de Buenos Aires, Argentina. Con
            conocimientos en diferentes lenguajes de programacion como C++,
            Javascript y ESQL ademas de frameworks y librerias como NodeJS,
            Express y React.
          </Typography>
        </Box>
      </div>
    </div>
  );
};

export default Main;
